
.card {
  padding: 10px 0 0 10px;
  padding: 0.625rem 0 0 0.625rem;
}
.card__game {
  max-width: 400px;
  max-width: 25rem;
  margin: 0 auto;
}
.card__table {
  padding-right: 10px;
  padding-right: 0.625rem;
  max-width: 400px;
  max-width: 25rem;
  margin: 0 auto;
}
.card__table .card--content {
    margin: 0 -15px;
    margin: 0 -0.9375rem;
}
.card__ranking {
  max-width: 600px;
  max-width: 37.5rem;
  margin: 0 auto;
}
@media only screen and (max-width: 39.9375em) {
.card__vote-winner {
    -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
            order: 1;
}
.card__upcoming {
    -webkit-box-ordinal-group: 3;
        -ms-flex-order: 2;
            order: 2;
}
}
.card--container {
  padding: 15px;
  padding: 0.9375rem;
  background-color: #fff;
  border-radius: 5px;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.2);
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
}
.card__game-focused .card--container {
    background-color: #fff6bf;
}
.card__game-voting-closed .card--container {
    background-color: #dde9f5;
}
.card__top10 .card--container {
    padding-bottom: 17px;
    padding-bottom: 1.0625rem;
}
.card--header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 10px;
  padding-bottom: 0.625rem;
  font-weight: 600;
}
.card__group .card--header {
    padding-bottom: 15px;
    padding-bottom: 0.9375rem;
    text-align: center;
}
.card__top10 .card--header {
    padding-bottom: 13px;
    padding-bottom: 0.8125rem;
}
.card--title {
  width: 100%;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  cursor: default;
}
.card--link {
  display: inline-block;
  padding: 1px 0;
  padding: 0.0625rem 0;
  font-size: 14px;
  font-size: 0.875rem;
  white-space: nowrap;
}
