
.ranking-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 -15px -1px;
  margin: 0 -0.9375rem -0.0625rem;
  padding: 9px 15px 6px;
  padding: 0.5625rem 0.9375rem 0.375rem;
  border-bottom: 1px solid #f5f5f5;
  cursor: default;
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
}
.ranking-row:last-of-type {
    border-bottom: none;
}
.ranking-row:hover {
    background-color: #f5f5f5;
}
.ranking-row__three:not(:last-of-type),
.ranking-row__ten:not(:last-of-type) {
  margin-bottom: 0;
  border-bottom: 1px solid #d4d4d4;
}
.ranking-row__user, .ranking-row__user:hover {
  background-color: #fff6bf;
}
.ranking--place {
  width: 24px;
  width: 1.5rem;
  font-weight: 600;
  text-align: right;
}
.ranking-row__top3 .ranking--place {
    color: #51832a;
}
.ranking-row__4to10 .ranking--place {
    color: #bf580c;
}
.ranking--name {
  width: calc(100% - 124px);
  padding-left: 10px;
  padding-left: 0.625rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.ranking--exact {
  color: #51832a;
  width: 30px;
  width: 1.875rem;
  text-align: right;
}
.ranking--tendency {
  color: #bf580c;
  width: 30px;
  width: 1.875rem;
  text-align: right;
}
.ranking--points {
  width: 40px;
  width: 2.5rem;
  font-weight: 600;
  text-align: right;
}
.ranking-extension {
  display: none;
}
@media only screen and (min-width: 35em) and (max-width: 39.9375em) {
.route-overview .ranking-extension {
    display: inline;
}
.route-overview .ranking--place {
    width: 40px;
    width: 2.5rem;
}
.route-overview .ranking--name {
    width: calc(100% - 200px);
    padding-left: 20px;
    padding-left: 1.25rem;
}
.route-overview .ranking--exact {
    width: 40px;
    width: 2.5rem;
    padding-right: 10px;
    padding-right: 0.625rem;
}
.route-overview .ranking--tendency {
    width: 50px;
    width: 3.125rem;
}
.route-overview .ranking--points {
    width: 70px;
    width: 4.375rem;
}
}
