
.fade-spinner-enter-active,
.fade-spinner-leave-active {
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.fade-spinner-enter,
.fade-spinner-leave-active {
  opacity: 0;
  will-change: opacity;
}
.button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #fff;
  background-color: #c21429;
  text-transform: uppercase;
  border-radius: 3px;
  cursor: pointer;
  outline: none;
  border: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
  will-change: background-color;
}
.button:hover, .button:focus {
    background-color: #a61123;
}
.button[disabled="disabled"] {
    background-color: #d89393;
    cursor: default;
    pointer-events: none;
}
.support-form .button {
    width: 100%;
    padding: 22px 25px;
    padding: 1.375rem 1.5625rem;
}
.button--spinner {
  width: 16px;
  width: 1rem;
  height: 16px;
  height: 1rem;
  margin-left: -22px;
  margin-left: -1.375rem;
  margin-right: 6px;
  margin-right: 0.375rem;
  opacity: 1;
  -webkit-animation: loading-button-spinner-rotation .7s infinite linear;
          animation: loading-button-spinner-rotation .7s infinite linear;
  border: 4px solid rgba(255, 255, 255, 0.2);
  border-top-color: #efefef;
  border-radius: 100%;
  -webkit-transition: .3s all ease;
  transition: .3s all ease;
}
.support-form .button--spinner {
    width: 30px;
    width: 1.875rem;
    height: 30px;
    height: 1.875rem;
    margin: -6px 10px -6px -40px;
    margin: -0.375rem 0.625rem -0.375rem -2.5rem;
}
@-webkit-keyframes loading-button-spinner-rotation {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
@keyframes loading-button-spinner-rotation {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
