
.main-menu {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.main-menu--items--wrapper {
  position: absolute;
  top: 100%;
  max-height: calc(100vh - 44px);
  right: 0;
  width: 100%;
  overflow-y: scroll;
}
.main-menu--items {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-menu--item {
  display: block;
}
.main-menu--item__user {
  position: relative;
  padding-top: 17px;
  padding-top: 1.0625rem;
  font-weight: 600;
  color: #fff;
  background-color: #c21429;
  text-align: center;
}
.main-menu--user--divider {
  margin: 0 16px;
  margin: 0 1rem;
  height: 1px;
  background-color: #fff;
}
.main-menu--user--name {
  position: relative;
  top: -9px;
  display: inline-block;
  margin: 0 auto;
  padding: 0 20px;
  padding: 0 1.25rem;
  background-color: #c21429;
}
.main-menu--link, .main-menu--toggle {
  display: block;
  padding: 14px 16px 12px;
  padding: 0.875rem 1rem 0.75rem;
  color: #fff;
  background-color: #c21429;
  text-decoration: none;
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
}
.main-menu--link:hover, .main-menu--toggle:hover {
    background-color: #cb4848;
    text-decoration: none;
}
.main-menu--link.router-link-active:not(.router-not-ready), .main-menu--toggle.router-link-active:not(.router-not-ready) {
    background-color: #a61123;
}
.main-menu--toggle {
  padding: 13px 16px 12px;
  padding: 0.8125rem 1rem 0.75rem;
}
.main-menu--toggle i {
    margin-right: 0;
}
.main-menu__open .main-menu--toggle {
    background-color: #cb4848;
}
@media only screen and (min-width: 48.0625em) {
.main-menu--items--wrapper {
    position: static;
    top: auto;
    right: 0;
    width: auto;
    overflow-y: auto;
    max-height: none;
}
.main-menu--toggle {
    display: none;
}
.main-menu--items {
    display: block !important;
}
.main-menu--items::before, .main-menu--items::after {
      content: "";
      display: table;
}
.main-menu--items::after {
      clear: both;
}
.main-menu--item {
    display: inline-block;
    float: left;
}
.main-menu--item__mobile {
    display: none;
}
}
