
.notifications {
  position: fixed;
  z-index: 5000;
  max-width: 100%;
  top: 49px !important;
}
.notification-wrapper {
  display: block;
  overflow: hidden;
  width: 100%;
  margin: 0;
  padding: 0;
}
.notification {
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: white;
  text-align: left;
}
.notification-title {
  font-weight: 600;
}
.vue-notification {
  font-size: 14px;
  font-size: 0.875rem;
  padding: 10px;
  padding: 0.625rem;
  margin: 0 10px 5px;
  margin: 0 0.625rem 0.3125rem;
  color: white;
  background: #44A4FC;
  border-left: 5px solid #187FE7;
}
.vue-notification.warn {
  background: #ffb648;
  border-left-color: #f48a06;
}
.vue-notification.error {
  background: #E54D42;
  border-left-color: #B82E24;
}
.vue-notification.success {
  background: #2d8647;
  border-left-color: #28673a;
}
.vn-fade-enter-active, .vn-fade-leave-active, .vn-fade-move {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.vn-fade-enter, .vn-fade-leave-to {
  opacity: 0;
}
