
@charset "UTF-8";
.playoff {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 -15px -1px;
  margin: 0 -0.9375rem -0.0625rem;
  padding-left: 15px;
  padding-left: 0.9375rem;
  color: #000;
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear;
}
.playoff:hover, .playoff:focus {
    color: #000;
    background-color: #f5f5f5;
    text-decoration: none;
}
.playoff--icon {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 60px;
  width: 3.75rem;
  padding: 0 15px 0 0;
  padding: 0 0.9375rem 0 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.playoff--icon img {
    max-width: 100%;
    width: 100%;
    height: auto;
    max-height: 50px;
}
.playoff--icon--text {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 40px;
  width: 2.5rem;
  height: 40px;
  height: 2.5rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 21px;
  font-size: 1.3125rem;
  color: #fff;
  background-color: #c21429;
  border-radius: 50%;
}
.playoff--content {
  position: relative;
  padding: 16px 60px 14px 0;
  padding: 1rem 3.75rem 0.875rem 0;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  border-bottom: 1px solid #f5f5f5;
}
.playoff:last-of-type .playoff--content {
    border-bottom: none;
}
.playoff--content:after {
    position: absolute;
    top: 50%;
    right: 10px;
    color: #757575;
    display: inline-block;
    font-family: "icomoon" !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    -webkit-font-feature-settings: normal;
            font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    vertical-align: baseline;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    width: 1em;
    content: "\E900";
    font-size: 28px;
    font-size: 1.75rem;
    -webkit-transform: rotate(-90deg) translateX(50%);
            transform: rotate(-90deg) translateX(50%);
}
.playoff--title {
  padding: 0 0 3px;
  padding: 0 0 0.1875rem;
}
.playoff--period {
  font-size: 14px;
  font-size: 0.875rem;
  color: #757575;
}
