
.vote {
  padding-bottom: 10px;
  padding-bottom: 0.625rem;
}
.vote-form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.vote--control {
  width: calc((100% - 56px)/2);
}
.vote--control--input input {
  height: 42px;
  height: 2.625rem;
  padding: 0 10px;
  padding: 0 0.625rem;
  -moz-appearance: textfield;
  color: #000;
  font-weight: 600;
  background-color: #fafafa;
  border: 1px solid #b5b5b5;
}
.vote--control--input input::-webkit-outer-spin-button, .vote--control--input input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
.vote--control--input input::-webkit-input-placeholder {
    color: #a1a1a1;
    font-weight: 400;
}
.vote--control--input input:-ms-input-placeholder {
    color: #a1a1a1;
    font-weight: 400;
}
.vote--control--input input::-ms-input-placeholder {
    color: #a1a1a1;
    font-weight: 400;
}
.vote--control--input input::placeholder {
    color: #a1a1a1;
    font-weight: 400;
}
.vote--control ~ .vote--control .vote--control--input input {
    text-align: right;
}
.vote--control--input input.has-error {
    -webkit-box-shadow: inset -2px -2px 0px #c21429, inset 2px 2px 0px #c21429;
            box-shadow: inset -2px -2px 0px #c21429, inset 2px 2px 0px #c21429;
}
@media only screen and (min-width: 25em) {
.vote--control--input input {
      padding: 0 20px;
      padding: 0 1.25rem;
}
}
@media only screen and (min-width: 40em) and (max-width: 43.6875em) {
.vote--control--input input {
      padding: 0 10px;
      padding: 0 0.625rem;
}
}
.vote--spacer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 56px;
  width: 3.5rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.game-votes {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin-top: 12px;
  margin-top: 0.75rem;
  height: 10px;
  height: 0.625rem;
  border-radius: 3px 3px 0 0;
  overflow: hidden;
  cursor: help;
}
.game-votes--win {
  background-color: #bcdea2;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.game-votes--draw {
  background-color: #ffd39f;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.game-votes--lose {
  background-color: #cb7680;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.vote--button {
  width: 100%;
  padding: 12px 15px;
  padding: 0.75rem 0.9375rem;
}
.game-votes + .vote--button.button {
    border-top-right-radius: 0;
    border-top-left-radius: 0;
}
.voting-closed {
  margin-top: 5px;
  margin-top: 0.3125rem;
  color: #fff;
  background-color: #4299c1;
  border-radius: 3px;
}
.voting-closed--info {
  padding: 10px;
  padding: 0.625rem;
}
.voting-closed--info div {
    font-weight: 600;
}
.voting-user {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 10px;
  padding-bottom: 0.625rem;
}
.voting-user--result {
  width: calc((100% - 56px)/2);
  padding-left: 21px;
  padding-left: 1.3125rem;
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 600;
}
.voting-user--result ~ .voting-user--result {
  padding-left: 0;
  padding-right: 22px;
  padding-right: 1.375rem;
  text-align: right;
}
.voting-points {
  padding: 10px;
  padding: 0.625rem;
  text-align: center;
  color: #000;
  background-color: #fff;
  border-top: 1px solid #248cb2;
  border-radius: 0 0 3px 3px;
}
.voting-points--result {
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: bold;
}
.voting-points--result span {
    cursor: help;
}
.voting-points--result__match {
  color: #51832a;
}
.voting-points--result__tendency {
  color: #bf580c;
}
.voting-points--result__lose {
  color: #a61123;
}
