
.vote-winner .vote--button {
  margin-top: 12px;
  margin-top: 0.75rem;
}
.vote-winner__closed {
  cursor: default;
}
.vote-winner--voted {
  padding: 15px 0 10px;
  padding: 0.9375rem 0 0.625rem;
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 600;
  text-align: center;
}
.vote-winner--not-voted {
  font-weight: 600;
  color: #a61123;
}
