
.overview-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 0 0 -10px;
  margin: 0 0 0 -0.625rem;
}
.overview-container__groups .card__group {
  width: 50%;
}
@media only screen and (min-width: 40em) {
.overview-container__groups .card__group {
      width: 25%;
}
}
@media only screen and (min-width: 80em) {
.overview-container__groups .card__group {
      width: 12.5%;
}
}
.overview-container__misc .card {
  width: 100%;
  padding-top: 20px;
  padding-top: 1.25rem;
}
@media only screen and (min-width: 40em) {
.overview-container__misc .card {
      width: 50%;
}
}
