html, body, .wrapper {
  height: 100%;
  min-height: 100%;
}
body {
  font-family: "Fira Sans", sans-serif;
  font-size: 16px;
  font-size: 1rem;
  background-color: #efefef;
}
.wrapper--bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: transparent no-repeat 50% 50%;
  background-size: cover;
  z-index: -1;
  opacity: 0;
  will-change: opacity;
  -webkit-transition: opacity 0.35s linear;
  transition: opacity 0.35s linear;
}
.route-login .wrapper--bg.loaded,
  .route-signup .wrapper--bg.loaded,
  .route-signed-up .wrapper--bg.loaded,
  .route-lost-password .wrapper--bg.loaded,
  .route-lost-password-requested .wrapper--bg.loaded,
  .route-reset-password .wrapper--bg.loaded,
  .route-reset-password-complete .wrapper--bg.loaded,
  .route-verify-email .wrapper--bg.loaded,
  .route-request-verification .wrapper--bg.loaded {
    opacity: 1;
}
.content {
  position: relative;
  padding-top: 44px;
  padding-top: 2.75rem;
  padding-bottom: 59px;
  padding-bottom: 3.6875rem;
  min-height: 100vh;
}
.verification-warning {
  padding: 20px;
  padding: 1.25rem;
  color: #333;
  background-color: #ddd;
  text-align: center;
}
.route-request-verification .verification-warning {
    display: none;
}
a {
  color: #c21429;
  text-decoration: none;
}
a:hover {
    text-decoration: underline;
}
.page-title {
  margin: 30px 0 10px;
  margin: 1.875rem 0 0.625rem;
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 600;
  text-align: center;
}
@media only screen and (min-width: 25em) {
.page-title {
      margin-bottom: 20px;
      margin-bottom: 1.25rem;
      font-size: 28px;
      font-size: 1.75rem;
}
}
.page-title--divider {
  color: #c21429;
}
.fade-enter {
  opacity: 0;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
