.app-pre-loader {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pre-load-spinner {
  width: 100px;
  width: 6.25rem;
  height: 100px;
  height: 6.25rem;
  -webkit-animation: pre-loading-spinner-rotation .7s infinite linear;
          animation: pre-loading-spinner-rotation .7s infinite linear;
  border: 10px solid rgba(0, 0, 0, 0.03);
  border-top-color: #d1d1d1;
  border-radius: 100%;
  -webkit-transition: .3s all ease;
  transition: .3s all ease;
}
@-webkit-keyframes pre-loading-spinner-rotation {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
@keyframes pre-loading-spinner-rotation {
