
.input {
  position: relative;
}
.input-icon--wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 65px;
  width: 4.0625rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 20px;
  font-size: 1.25rem;
}
@media only screen and (max-width: 24.9375em) {
.support-box .input-icon--wrapper {
      width: 50px;
      width: 3.125rem;
}
}
.input-icon {
  color: #686868;
  -webkit-transition: color 0.15s linear;
  transition: color 0.15s linear;
}
input:focus + .input-icon--wrapper > .input-icon {
    color: #c21429;
}
input {
  display: block;
  width: 100%;
  height: 62px;
  height: 3.875rem;
  padding: 0 30px;
  padding: 0 1.875rem;
  font-size: 18px;
  font-size: 1.125rem;
  color: #686868;
  background: #e6e6e6;
  border: none;
  border-radius: 3px;
  outline: none;
}
input.has-icon {
    padding: 0 30px 0 65px;
    padding: 0 1.875rem 0 4.0625rem;
}
input.has-error {
    -webkit-box-shadow: inset -3px -3px 0px #c21429, inset 3px 3px 0px #c21429;
            box-shadow: inset -3px -3px 0px #c21429, inset 3px 3px 0px #c21429;
}
@media only screen and (max-width: 24.9375em) {
.support-box input {
      height: 50px;
      height: 3.125rem;
}
.support-box input.has-icon {
        padding: 0 15px 0 50px;
        padding: 0 0.9375rem 0 3.125rem;
}
}
