
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 10px;
  padding: 0.625rem;
  background-color: #fff;
  border-top: 1px solid #e6e6e6;
}
@media only screen and (min-width: 31.25em) {
.footer {
      text-align: center;
}
}
.footer--link:hover {
  text-decoration: underline;
}
.made-with-love {
  position: absolute;
  top: 14px;
  top: 0.875rem;
  right: 20px;
  right: 1.25rem;
  display: inline-block;
  font-size: 12px;
  margin-left: 10px;
  margin-left: 0.625rem;
}
.made-with-love .heart {
    display: inline-block;
    font-size: 16px;
    font-size: 1rem;
    line-height: 0;
    color: #e25555;
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
}
.made-with-love .twitter-handle {
    color: #1F98C7;
}
