
.flash-message {
  padding: 10px 15px;
  padding: 0.625rem 0.9375rem;
  margin-bottom: 20px;
  margin-bottom: 1.25rem;
  line-height: 1.4;
  border-radius: 3px;
}
@media only screen and (min-width: 25em) {
.flash-message {
      padding: 20px;
      padding: 1.25rem;
}
}
.flash-message__error {
  color: #fff;
  background-color: #cb4848;
}
