
.select {
  width: 100%;
  position: relative;
}
.select-icon--wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 50px;
  width: 3.125rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 20px;
  font-size: 1.25rem;
}
.select-icon {
  color: #686868;
  -webkit-transition: color 0.15s linear;
  transition: color 0.15s linear;
}
select:focus + .select-icon--wrapper > .select-icon {
    color: #c21429;
}
select {
  display: block;
  width: 100%;
  height: 42px;
  height: 2.625rem;
  padding: 0 60px 0 30px;
  padding: 0 3.75rem 0 1.875rem;
  font-size: 18px;
  font-size: 1.125rem;
  background-color: #fafafa;
  border: 1px solid #b5b5b5;
  color: #000;
  font-weight: 600;
  border-radius: 3px;
  outline: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}
select::-ms-expand {
    display: none;
}
select.has-error {
    -webkit-box-shadow: inset -3px -3px 0px #c21429, inset 3px 3px 0px #c21429;
            box-shadow: inset -3px -3px 0px #c21429, inset 3px 3px 0px #c21429;
}
