
.support-box {
  padding: 20px;
  padding: 1.25rem;
  width: 100%;
  max-width: 440px;
  max-width: 27.5rem;
  background-color: #fff;
  -webkit-box-shadow: 0 0 40px rgba(0, 0, 0, 0.6);
          box-shadow: 0 0 40px rgba(0, 0, 0, 0.6);
  border-radius: 3px;
}
@media only screen and (min-width: 25em) {
.support-box {
      padding: 40px;
      padding: 2.5rem;
}
}
.support-box--title {
  margin: 0 0 20px;
  margin: 0 0 1.25rem;
}
@media only screen and (min-width: 25em) {
.support-box--title {
      margin-bottom: 30px;
      margin-bottom: 1.875rem;
}
}
.support-box--footer {
  color: #999999;
  text-align: center;
}
.support-box--footer p {
    margin: 0 0 1em;
}
.support-box--footer p:last-child {
      margin-bottom: 0;
}
.animate-shake {
  -webkit-animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) forwards 1;
          animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) forwards 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}
@-webkit-keyframes shake {
10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
}
20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
}
30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
}
40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
}
}
@keyframes shake {
10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
}
20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
}
30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
}
40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
}
}
